.error {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $pink;
    margin-top: 5vh;
    margin-bottom: 15vh;

    &_number {
        font-size: 350px;
        font-weight: 900;
        margin-top: 5vh;

        @include for-tablet {
            font-size: 200px;
            margin-top: 10vh;
        }

        @include for-mobile {
            font-size: 150px;

        }
    }

    &_msg {
        margin-top: 5vh;
        font-size: 35px;
        text-align: center;

        @include for-tablet {
            font-size: 30px;
        }

        @include for-mobile {
            font-size: 20px;
        }
    }

    &_linkHome {
        text-align: center;
        margin-top: 15vh;
        color: $pink;
        font-size: 20px;

        @include for-tablet {
            font-size: 16px;
        }

        @include for-mobile {
            font-size: 14px;
        }
    }
}